

export function onReady(action?: () => void): Promise<void> | undefined{
  let resolvePromise: () => void;
  document.addEventListener("DOMContentLoaded", (e: any) => {
    if(action) action(); else resolvePromise()
  })
  if(!action) return new Promise<void>(resolve => resolvePromise = resolve)
}

type SortVal = string | number
type DeepProperties = string | string[]
type AscendingDescending = 'ascending' | 'descending'

/**
 * 
 * @param {any[]} array 
 * @param ascendingDescending 
 * @param deepProperties 
 * @returns {T} Return sorted array
 * @example sort([{x: 2}, {x: 1}, {x: 3}], 'descending', 'x')
 * output: [{x:3},{x:2},{x:1}]
 * 
 */
export const sort = <
  T extends any[], 
  T2 extends DeepProperties
>(
  array: T,
  ascendingDescending: AscendingDescending = 'ascending',
  deepProperties?: T2
): T => {  
  return array.sort((a, b) => (
    (getSortVal(a) < getSortVal(b) ? 1 : -1) *
    (ascendingDescending == 'descending' ? -1 : 1)
  ))
  
  function getSortVal(rootObj: any): SortVal{
    return (
      !deepProperties ? rootObj : 
      typeof deepProperties == 'string' ? rootObj[deepProperties] : 
      deepProperties.reduce((deepProp, prop) => deepProp[prop], rootObj)
    )
  }
}

export function toDecimalPlaces(number: number, decimalPlaces: number): number{
  return Math.round(number * Math.pow(10,decimalPlaces)) / Math.pow(10,decimalPlaces)
}


const existingTimeOuts: {[key: string]: {timeout: any, reject: any}} = {}
export function debounce(milliseconds: number, name: string): Promise<void>{

  if(existingTimeOuts[name]){
    clearTimeout(existingTimeOuts[name].timeout)
    existingTimeOuts[name].reject()
    delete existingTimeOuts[name]
  }

  return new Promise((resolve, reject) => {
    existingTimeOuts[name] = {
      timeout: setTimeout(resolve, milliseconds),
      reject
    }
  })

}


export function getElem<T extends HTMLElement>(selector: string, container?: HTMLElement, handleError?: 'throw on fail'): T{
  const elem: T = (container || document).querySelector(selector)!;
  if(!elem && handleError == 'throw on fail'){
    throwSelectorError(selector)
  }
  return elem
}

export function getElems<T extends HTMLElement>(selector: string, container?: HTMLElement, handleError?: 'throw on fail'): T[]{
  const elems: T[] = Array.from((container || document).querySelectorAll(selector));
  if(!elems.length && handleError == 'throw on fail'){
    throwSelectorError(selector)
  }
  return elems
}


export function throwSelectorError(selector: string){
  throw `Could not find element with selector ${selector}`
}


export function waitForNextFrame(){
  return new Promise(resolve => requestAnimationFrame(resolve));
}

export function onScroll(action: () => void){
  let previousScrollAmount: number;
  doActionThenWaitForNextFrame();

  function doActionThenWaitForNextFrame() {
    const currentScrollAmount = window.scrollY;

    if (currentScrollAmount !== previousScrollAmount) {
      action();
      previousScrollAmount = currentScrollAmount;
    }
    // on next animation frame, trigger same function
    requestAnimationFrame(doActionThenWaitForNextFrame);
  }
}

export function elemIsInView(elem: HTMLElement): boolean {
  // top and bottom values are relative to window view
  const { top, bottom } = elem.getBoundingClientRect();
  const elemOutOfView = top > window.innerHeight || bottom < 0;
  return elemOutOfView ? false : true;
}

// on resize
const actions: (() => void)[] = [];
export function onResize(action: () => void) {
  actions.push(action);
}
window.addEventListener('resize', (_) => actions.forEach((action) => action()));

export function loop(number: number, action: (i?: number) => void) {
  for (let i = 0; i < number; i++) {
    action(i);
  }
}

export const randomNumber = ({ digits }: { digits: number }): number => {
  const num = Math.pow(10, digits - 1);
  return Math.round(Math.random() * num);
};

export const timer = ({ seconds }: { seconds: number }) =>
  new Promise((resolve) => setTimeout(resolve, seconds * 1000));
