
import { getFormat, printMoney } from '@process-creative/slate-theme-tools';
import { Cart } from '../tools/cart/cart';
import { LineItem } from '../tools/cart/line-item';
import { getElem,  getElems,  onReady } from '../tools/utilities/helper-functions';


const SEL_CLASS_DESCRIPTION = '.product__description';
const SEL_CLASS_PROPERTY = '.product__description__property';
const SEL_CLASS_PRICE = '.product__price .order-summary__emphasis';
const SEL_CLASS_PROD_NAME = '.product__description__name'
const SEL_CLASS_PROD_VARIANT = '.product__description__variant'
const SEL_CLASS_DISCOUNT_MESSAGE = '.reduction-code'

/* 
strategy
- determines what dependencies are available and picks the best option
- sets each bundles price and name and removes the rest

*/

type LineInfo = {
  lineIndex: number,
  lineItem?: LineItem,
  rowElem: HTMLElement
  originalPrice?: number
}


type CartBundle = {
  bundleId: string
  product: LineInfo
  variants: LineInfo[]
}

["DOMContentLoaded", "load", "change"].forEach(event =>
  document.addEventListener(event, transformCartBundleItems)
)

function transformCartBundleItems(){

  
  let cartData: Cart
  let rowElems: HTMLElement[]

  let cartBundles: CartBundle[]


  setup()

  function setup(){

    try{rowElems = getElems('[data-product-id]')}catch(e){
      try{rowElems = getElems('[data-order-summary-section] > *')}catch(e){
        throw 'Failed to select row elems'
      }
    }

    try{cartData = getCartData()}catch(e){
      console.error('Failed to get cart data ' + e)
    }

    cartBundles = getCartBundles()

  
    forEachProductBundle(bundle => {
      const totalPrice = getBundleTotalPrice(bundle)
      const childTitles = getBundleChildTitles(bundle)
      removeExistingBundleProperties(bundle);
      removeBundleDiscountMessage(bundle);
      updateBundleComparePrice(bundle);
      setBundlePriceToTotal(bundle, totalPrice);
      setBundleItemNames(bundle, childTitles);
      removeBundleChildVariants(bundle);
    })
  }
    
  //implementation

  function getCartData(){
    const dataElem = getElem('[data-checkout-json]')?.innerHTML
    const cartData = dataElem ? JSON.parse(dataElem): undefined
    if(!cartData?.items.length) throw 'Failed to get cart data from dom json'
    return cartData

  }

  function getCartBundles(): CartBundle[]{

    try{
      return getBundlesWithCartData()
    } catch (error){
      console.warn(error)
      return getBundlesWithClasses()
    }
    
    function getBundlesWithCartData(): CartBundle[]{
      return cartData.items.reduce((bundles, lineItem, lineIndex) => {
        const {isProductBundle} = lineItem.properties
        if(isProductBundle){
          const rowElem = rowElems[lineIndex]
          const bundle: CartBundle = {
            bundleId: isProductBundle,
            product: {lineIndex,lineItem,rowElem},
            variants: cartData.items.reduce((variants, lineItem, lineIndex) => {
              const originalPrice = lineItem.variant?.price
              const {isChildVariant} = lineItem.properties           
              if(isChildVariant == isProductBundle){
                const rowElem = rowElems[lineIndex]
                variants.push({lineIndex,lineItem,rowElem,originalPrice})
              }
              return variants
            }, <LineInfo[]>[])
          }
          bundles.push(bundle)
        }          
        return bundles
      }, <CartBundle[]>[])
  
    }

    function getBundlesWithClasses(){
      return rowElems.reduce((bundles, rowElem, rowIndex) => {
        const propertyElem = getElem(SEL_CLASS_PROPERTY, rowElem)    
        const bundleId = propertyElem?.innerHTML.split('isProductBundle:')[1].trim();
        if(!bundleId){
          throw('Failed to get bundle ID from dom element ')
        } else {
          const bundle: CartBundle = {
            bundleId: bundleId,
            product: {lineIndex: rowIndex, rowElem},
            variants: rowElems.reduce((variants, rowElem, rowIndex) => {    
              const childBundleId = propertyElem!.innerHTML.split('isChildVariant:')[1].trim();
              
              if(childBundleId == bundleId){
                variants.push({lineIndex: rowIndex, rowElem})
              }
              return variants
            }, <LineInfo[]>[])
          }
          bundles.push(bundle)
        }          
        return bundles
      }, <CartBundle[]>[])
    }
  }
  
    
  function forEachProductBundle(action: (bundle: CartBundle) => void) {  
    cartBundles.forEach(cartBundle => {
      action(cartBundle);
    })
    cartData.items
  }
  

  function getBundleTotalPrice(bundle: CartBundle): number{
    try{
      if(!bundle.variants.every(v => v.lineItem)) throw 'no line item data for bundle variants'
      return bundle.variants.reduce((totalPrice, variant) => 
        totalPrice + variant.lineItem!.price
      , 0)
    } catch (error){
      console.warn(error)
      return bundle.variants.reduce((total, {rowElem}) => {
        const priceNum = Number(getElem(SEL_CLASS_PRICE, rowElem)!.innerHTML.replace('$', ''))
        return total + priceNum
      }, 0)
    }


  }
  
  function getBundleChildTitles({product: {lineItem}, variants}: CartBundle): string[]{
    try{
      if(!lineItem) throw 'Bundle does not have line item data, need to read titles from elems'
      return lineItem!.properties._childTitles.split('|')
    } catch(e){
      return variants.reduce((titles: string[], {rowElem}) => {
        const name = getElem(SEL_CLASS_PROD_NAME, rowElem)?.innerHTML
        const variant = getElem(SEL_CLASS_PROD_VARIANT, rowElem)?.innerHTML
        if(!name || ! variant) throw 'Failed to find variant title classes'
        const variantTitle = `${name} ${variant}`
        return [...titles, variantTitle]      
      }, [])
    }
  }

  
  function removeExistingBundleProperties({product: {rowElem}}: CartBundle){
    const bundlePropertyElems = getElems(SEL_CLASS_PROPERTY, rowElem)
    bundlePropertyElems.forEach(propertyElem => propertyElem.remove())
  }

  function updateBundleComparePrice({product: {rowElem}, variants}: CartBundle){

    const totalOriginalPrice = variants.reduce((total, variant) => {
      return total + (variant.originalPrice || 0)
    }, 0)
    totalOriginalPrice

    const comparePriceElem = getElem('del', rowElem)
    
    if(comparePriceElem){
      comparePriceElem.innerHTML = `$${totalOriginalPrice/100}`;
    }
  }

  
  function removeBundleDiscountMessage({product: {rowElem}}: CartBundle){
    const bundleMessageElem = getElem(SEL_CLASS_DISCOUNT_MESSAGE, rowElem)
    bundleMessageElem?.remove()
  }
  
  
  function setBundlePriceToTotal({product: {lineItem, rowElem}}: CartBundle, totalPrice: number){
    const priceElem = getElem(SEL_CLASS_PRICE, rowElem)
    if(!priceElem) throw 'Failed to set bundle price from cart and elem'
    priceElem.innerHTML = `$${totalPrice/100}`;
  }
  
  function setBundleItemNames({product: {rowElem}}: CartBundle, childTitles: string[]){
    const bundleTitlesContainer = getElem(SEL_CLASS_DESCRIPTION, rowElem)

    const dummy = document.createElement('div');
    dummy.innerHTML = ``;
    childTitles.forEach((title: string, i: number) => {
      dummy.innerHTML += i !== 0 ? `<br>` : ``;
      dummy.innerHTML += `- ${title}`;
    });
  
    
    dummy.classList.add('product__description__property', 'order-summary__small-text');
  
    bundleTitlesContainer?.append(dummy);
  
  }
  
  function removeBundleChildVariants({variants}: CartBundle){  
    variants.forEach(variant => variant.rowElem.remove());
  }
}